.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-h {
  color: white;
  font-size: 24px;
  font-weight: bold;
}

.app-p {
  color: white;
  font-size: 21px;
}


.app-frame {
  width: 100%;
  height: 80vh;
  margin: 0 auto;
  /* outline: 2px steelblue solid; */
  overflow: hidden;
  position: relative;

  background-image: url('assets/bg.png');
  /* opacity: 0.6; */
  background-size: 600px 544px;
  background-repeat: no-repeat;
  background-position: center 120px;

  /* display: flex;
  align-items: center;
  justify-content: center; */
}

.app-frame > div {
  width: 100%;
  height: 80vh;
}

.app-img {
  position: relative;
  width: auto;
  touch-action: none;
  /* width: 100%; */
  /* object-fit: contain; */
}

.platform {
  position: absolute;
  width: 158px;
  height: 49px;
  bottom: 15px;
  background-color: #e8e8e8;
  background-image: url('assets/platform.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.platform::after {
  content: '';
  position: absolute;
  /* z-index: -1; */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -51%);
  width: 186px;
  height: 67px;
  /* background-color: red; */
  background-image: url('assets/platform_back.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.ball {
  position: absolute;
  left: 50%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #f2a3c7;
  background-image: url('assets/ball.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.bricks {
  position: absolute;
  /* top: 5px; */
  left: 0;
  width: 100%;
  height: 50px;
  /* background-color: yellow; */
}

.brick {
  position: absolute;
  width: 70px;
  height: 20px;
  background-color: #87bce6;
  background-image: url('assets/brick.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  border-radius: 0.3rem;
}

.brick--special {
  background-color: #ff539e;
  background-image: url('assets/brick-special.png');
}
