.btn1 {
  top: 0;
}

.btn2 {
  top: 20px;
}

.border-bottom {
  width: 100%;
  height: 3px;
  background-color: #2f2771;
}

.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 10;
  background-color: rgba(47, 39, 113, 0.6);
  
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.visible {
  display: flex;
}

.overlay p {
  font-size: 18px;
  color: white;
}

.btn-bricks {
  width: 60%;
  padding: 25px 0;
  margin: 0 auto;
  margin-bottom: 25px;
  background-color: white;
  border-radius: 5px;
  transition: all 0.2s;

  color: #2f2771;
  font-size: 28px;
  font-weight: bold;
  line-height: 32px;
}

.smaller {
  font-size: 18px;
  line-height: 22px;
}

.btn-bricks:active {
  transform: scale(0.95);
}

.final-btn {
  /* display: block;
  position: initial; */
  width: 60%;
  padding: 10px 20px;
  background-color: #2f2771;
  color: white;
  font-size: 21px;
  font-weight: bold;
  border: 0;
  outline: none;
  border-radius: 8px;
  transition: all 0.2s;
}

.final-btn:active {
  transform: scale(0.95);
}

.start {
  padding-left: 30px;
  padding-right: 30px;
}

.final {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.final-img {
  width: 70%;
  margin-top: 40px;
}

.final-img--2 {
  width: 90%;
  margin-top: 0px;
}

.bubble {
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-image: url('assets/bubble1.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0;
  /* transform: scale(0.3);
  transition: all 0.3s; */
}

@keyframes bubbleToTop {
  0% {
    opacity: 0;
    transform: scale(0.2) translateY(0) rotate(-10deg);
  }
  20% {
    opacity: 1;
    transform: scale(1) translateY(0) rotate(10deg);
  }
  40% {
    opacity: 1;
    transform: scale(1) translateY(0) rotate(-10deg);
  }
  50% {
    opacity: 1;
    transform: scale(1) translateY(0) rotate(0);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(-300px);
  }
}

.bubble-visible {
  /* opacity: 1;
  transform: scale(1); */
  animation: bubbleToTop 6s ease forwards;
}
