.girl {
  position: absolute;
  width: 160px;
  height: 360px;
  top: 30vh;
  left: -10px;
  /* outline: 1px red solid; */

  background-image: url('assets/girl_01.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  transform: translateX(-200px) rotate(-20deg);
}

@keyframes girlReveal1 {
  0% {
    transform: translateX(-200px) rotate(-20deg);
  }
  60%, 100% {
    transform: translateX(0px) rotate(0deg)
  }
}

.girl-visible {
  animation: girlReveal1 0.9s linear 2 alternate forwards;
}